import { z } from 'zod';

export const LocaleSchema = z.record(
  z.string().regex(/^[a-z]{2}$/), // Ensures valid locale codes (e.g., en, nl, de)
  z.record(z.string(), z.string()), // Ensures all fields inside each locale are strings
);

export const MetaSchema = z.record(
  z.union([LocaleSchema, z.string().min(1), z.number(), z.boolean()]),
);

export interface LocaleInterface {
  [key: string]: Record<string, string>;
}

export interface MetaInterface {
  [key: string]: string | LocaleInterface;
}
